<template>
  <div class="shareList">
    <!--  -->
    <hmnavbar @back="$router.push('/find')" title=""></hmnavbar>
    <!--  -->
    <div class="search" @click="$router.push('/search')">
      <i class="iconfont iconicon_search"></i>
      <span></span>
    </div>
    <!--  -->
    <van-list
      v-model="loading"
      :finished="finished"
      finished-text=""
      @load="onload"
    >
      <share-item
        :item="item"
        v-for="(item, index) in list"
        :key="index"
      ></share-item>
    </van-list>
  </div>
</template>

<script>
import { getShareApi } from '../../../api/articles'
export default {
  data () {
    return {
      // 
      list: [],
      // list 
      loading: false,
      // list 
      finished: false,
      // 
      // 
      start: -5,
      // 
      limit: 5,
      // 
      total: 0
    }
  },
  methods: {
    // list  & 
    async onload () {
      // 
      this.start += this.limit
      const res = await getShareApi({
        start: this.start,
        limit: this.limit
      })
      // 
      // this.list = res.data.list
      // 
      this.list.push(...res.data.list)
      this.total = res.data.total
      // 
      this.loading = false
      // 
      if (this.list.length === this.total) {
        this.finished = true
      }
    }
  }
}
</script>

<style lang="less" scoped>
.shareList {
  .search {
    margin: 10px 15px;
    height: 34px;
    background-color: #f7f4f5;
    border-radius: 20px;
    font-size: 14px;
    color: #c4c3c3;
    display: flex;
    align-items: center;
    justify-content: center;
    i {
      font-size: 20px;
      margin-right: 6px;
    }
  }
}
</style>
