<template>
  <div class="search">
    <!--  -->
    <hmnavbar @back="$router.push('/shareList')" title=""></hmnavbar>
    <!--  -->
    <van-search
      v-model="value"
      show-action
      placeholder=""
      @search="search"
      @cancel="onCancel"
      shape="round"
    />
    <div v-if="isShowHot">
      <!--  -->
      <div class="hotsearch">
        <van-cell>
          <template #title>
            <h3 class="mytitle"></h3>
          </template>
        </van-cell>
        <div class="tags">
          <div
            @click="search(item)"
            class="tag"
            v-for="(item, index) in hotList"
            :key="index"
          >
            {{ item }}
          </div>
        </div>
      </div>
      <!--  -->
      <div class="hotsearch">
        <van-cell>
          <template #title>
            <h3 class="mytitle"></h3>
          </template>
          <template #default>
            <span @click="clear"></span>
          </template>
        </van-cell>
        <div class="tags">
          <div
            @click="search(item)"
            class="tag"
            v-for="(item, index) in historyList"
            :key="index"
          >
            {{ item }}
          </div>
        </div>
      </div>
    </div>
    <div class="article" v-else>
      <!--
        onload:
          list 
          list 
       -->
      <van-list
        v-model="loading"
        :finished="finished"
        finished-text=""
        @load="onload"
      >
        <share-item
          :item="item"
          v-for="(item, index) in shareList"
          :key="index"
        ></share-item>
      </van-list>
    </div>
  </div>
</template>

<script>
import { getHotSearch, getShareApi } from '../../../api/articles'
import { setLocal, getLocal, removeLocal } from '../../../utils/local'
export default {
  data () {
    return {
      // 
      value: '',
      // 
      hotList: [],
      // 
      isShowHot: true,
      // 
      shareList: [],
      loading: false,
      finished: false,
      start: -5,
      limit: 5,
      total: 0,
      // 
      historyList: []
    }
  },
  methods: {
    // 
    clear () {
      this.$dialog
        .confirm({
          title: '',
          message: ''
        })
        .then(() => {
          //  hsitoryList
          this.historyList = []
          //  localstorage
          removeLocal('history')
        })
        .catch(() => {
          console.log('')
        })
    },
    // list  load 
    async onload () {
      this.start += this.limit
      const res = await getShareApi({
        start: this.start,
        limit: this.limit,
        q: this.value
      })
      //  title 
      res.data.list.forEach(item => {
        //  item  title 
        item.title = item.title.replace(
          this.value,
          `<span style="color:red">${this.value}</span>`
        )
      })
      this.shareList.push(...res.data.list)
      this.total = res.data.total
      // 
      this.loading = false
      // 
      if (this.shareList.length >= this.total) {
        this.finished = true
      }
    },
    // 
    search (item) {
      // 
      this.value = item
      // 
      this.isShowHot = false
      // 
      this.historyList.unshift(item)
      // 
      this.historyList = [...new Set(this.historyList)]
      // 
      this.historyList.splice(5)
      //  historyList  localstorage 
      setLocal('history', JSON.stringify(this.historyList))
    },
    // // 
    // // 
    // onSearch (value) {
    //   console.log('onSearch')
    //   console.log(value)
    // },
    // 
    onCancel () {
      //  & 
      this.isShowHot = true
      // 
      this.shareList = []
      this.loading = false
      this.finished = false
      this.start = -5
      this.total = 0
    }
  },
  async created () {
    //  localostrage 
    this.historyList = JSON.parse(getLocal('history')) || []
    // 
    const res = await getHotSearch()
    this.hotList = res.data
  }
}
</script>

<style lang="less" scoped>
.search {
  .hotsearch {
    .mytitle {
      font-size: 20px;
      font-weight: 400;
    }
    .tags {
      padding: 15px;
      display: flex;
      flex-wrap: wrap;
      .tag {
        background-color: #eceaea;
        padding: 4px 6px;
        font-size: 14px;
        margin-right: 10px;
      }
    }
  }
}
</style>
