<template>
  <div class="market">
    <!--  -->
    <hmnavbar title="" @back="$router.push('/find')"></hmnavbar>
    <!--  -->
    <van-dropdown-menu>
      <van-dropdown-item :title="selCity" ref="mycity">
        <!--  -->
        <van-search
          v-model="cityValue"
          shape="round"
          background="#f7f4f5"
          placeholder=""
        />
        <!--  -->
        <van-index-bar>
          <template v-for="(value, key) in cityList">
            <van-index-anchor :index="key" :key="key" />
            <van-cell
              @click="changeCity(item)"
              v-for="(item, index) in value"
              :key="index"
              :title="item[0]"
            />
          </template>
        </van-index-bar>
      </van-dropdown-item>
      <van-dropdown-item :title="selPosition" ref="myposition">
        <van-cell
          @click="changePosition(item)"
          v-for="(item, index) in positionList"
          :key="index"
          :title="item"
        ></van-cell>
      </van-dropdown-item>
    </van-dropdown-menu>
    <!--  -->
    <div class="gongzi">
      <h3></h3>
      <canvas id="gongzi" width="375" height="260"></canvas>
    </div>
  </div>
</template>

<script>
//  api
import { getIndexesApi, getDataApi } from '@/api/market'
// 
import F2 from '@antv/f2'
export default {
  data () {
    return {
      // 
      cityValue: '',
      // 
      cityList: {},
      // 
      selCity: '',
      // 
      selPosition: '',
      // 
      positionList: [],
      // 
      gzchart: null,
      // 
      percentSalary: []
    }
  },
  async created () {
    // 
    const res = await getIndexesApi()
    this.cityList = res.data
    // 
    // 
    this.selCity = this.cityList[''][0][0]
    // 
    this.positionList = this.cityList[''][0][1]
    // 
    this.selPosition = this.cityList[''][0][1][0]
    // 
    this.getData()
  },
  mounted () {
    this.$nextTick(() => {
      // 
      this.initdraw()
    })
  },
  methods: {
    // 
    async getData () {
      const res = await getDataApi({
        city: this.selCity,
        position: this.selPosition
      })
      // 
      this.percentSalary = res.data.percentSalary
      // 
      this.drawGongozi()
    },
    // 
    initdraw () {
      // Step 1:  Chart 
      this.gzchart = new F2.Chart({
        id: 'gongzi',
        pixelRatio: window.devicePixelRatio // 
      })
    },
    drawGongozi () {
      // 
      this.gzchart.clear()
      // 
      //  a: 1
      this.percentSalary.forEach(item => {
        item.a = '1'
      })
      // 
      const map = {}
      this.percentSalary.forEach(function (obj) {
        map[obj.assetType] = obj.percent + '%'
      })
      // 
      this.gzchart.source(this.percentSalary, {
        percent: {
          formatter: function formatter (val) {
            return val + '%'
          }
        }
      })
      // 
      this.gzchart.tooltip(false)
      // 
      this.gzchart.legend({
        position: 'right',
        itemFormatter: function itemFormatter (val) {
          return val + '    ' + map[val]
        }
      })
      // 
      this.gzchart.coord('polar', {
        transposed: true,
        innerRadius: 0.6,
        radius: 0.8
      })
      this.gzchart.axis(false)
      // 
      this.gzchart
        .interval()
        .position('a*percent')
        .color('assetType', ['#FE5D4D', '#3BA4FF', '#737DDE'])
        .adjust('stack')
      // 
      this.gzchart.guide().html({
        position: ['50%', '45%'],
        html: `<div style="width: 250px;height: 40px;text-align: center;">
      <div style="font-size: 16px"></div>
      <div style="font-size: 24px">1190</div>
    </div>`
      })
      console.log('')
      // 
      this.gzchart.render()
    },
    // 
    changeCity (item) {
      // 
      this.selCity = item[0]
      // 
      this.positionList = item[1]
      // 
      this.selPosition = this.positionList[0]
      // 
      this.$refs.mycity.toggle()
      // 
      this.getData()
    },
    changePosition (item) {
      // 
      this.selPosition = item
      // 
      this.$refs.myposition.toggle()
      // 
      this.getData()
    }
  }
}
</script>

<style lang="less" scoped>
.market {
  /deep/ .van-cell.van-cell--borderless.van-field {
    background: #fff;
    border-radius: 20px;
  }
  /deep/ .van-index-anchor {
    background: #f7f4f5;
  }
  .gongzi {
    h3 {
      padding: 20px;
      font-size: 18px;
      font-weight: 400;
    }
  }
}
</style>
