<template>
  <div class="question">
    <!--  -->
    <van-dropdown-menu>
      <van-dropdown-item ref="mycity">
        <template #title>
          <div class="box">
            
            <span>{{ selCity }}</span>
          </div>
        </template>
        <van-index-bar>
          <template v-for="(value, key) in cityList">
            <van-index-anchor :index="key" :key="key" />
            <van-cell
              @click="changeCity(item)"
              v-for="(item, index) in value"
              :title="item"
              :key="index + key"
            />
          </template>
        </van-index-bar>
      </van-dropdown-item>
    </van-dropdown-menu>
    <!--  -->
    <div class="positionbox">
      <div
        class="tags"
        :class="{ active: currentIndex === index }"
        v-for="(item, index) in postionList"
        :key="index"
        @click="changeIndex(index)"
      >
        {{ item.name }}
      </div>
    </div>
    <!--  -->
    <div class="contentbox">
      <div class="one item">
        <i class="iconfont iconicon_mine_cuoti"></i>
        <span></span>
      </div>
      <div class="two item">
        <i class="iconfont iconicon_mine_tikushoucang"></i>
        <span></span>
      </div>
      <div class="three item">
        <i class="iconfont iconicon_mine_qiyeshoucang"></i>
        <span></span>
      </div>
      <div class="four item">
        <i class="iconfont iconicon_mine_mianjing"></i>
        <span></span>
      </div>
      <div class="center">
        <van-circle
          class="circle"
          v-model="currentRate"
          stroke-width="70"
          layer-color="transparent"
          :rate="rate"
          :speed="100"
        >
          <template>
            <div class="text">
              <div class="title"></div>
              <div class="num">{{ rightNum }}/{{ totalNum }}</div>
            </div>
          </template>
        </van-circle>
      </div>
    </div>
    <!--  -->
    <div class="total">
      <div class="text"></div>
      <div class="num">{{ totalNum }}</div>
    </div>
    <!--  -->
    <div class="btn">
      <van-button
        @click="toInterview"
        class="mybtn"
        size="large"
        color="#00b8d4"
        ></van-button
      >
    </div>
  </div>
</template>

<script>
// 
import { getQuestionDataApi } from '@/api/question.js'
import { mapState } from 'vuex'
export default {
  data () {
    return {
      currentRate: 0,
      // 
      rate: 0,
      // 
      cityList: {},
      // 
      cityPosition: {},
      // 
      selCity: '',
      // 
      postionList: [],
      // 
      currentIndex: 0,
      // 
      totalCount: 0,
      //  &  0
      rightNum: 0,
      totalNum: 0
    }
  },
  computed: {
    ...mapState(['user'])
  },
  methods: {
    // 
    toInterview () {
      this.$router.push(
        `/interview/${this.postionList[this.currentIndex].id}/${this.selCity}`
      )
    },
    //  0 
    myanimat () {
      //  2000
      var totalTime = 2000
      //  0  user.correctQuestions.length  25
      var timer1 = setInterval(() => {
        this.rightNum += 1
        //  rightNum === user.correctQuestions.length ,
        if (this.rightNum >= this.user.correctQuestions.length) {
          clearInterval(timer1)
        }
      }, totalTime / this.user.correctQuestions.length)
      //  0  totalCount 156
      var timer2 = setInterval(() => {
        this.totalNum += 1
        //  rightNum === user.correctQuestions.length ,
        if (this.totalNum >= this.totalCount) {
          clearInterval(timer2)
        }
      }, totalTime / this.totalCount)
      // 
      setTimeout(() => {
        this.rate = Math.round(
          (this.user.correctQuestions.length / this.totalCount) * 100
        )
      }, 2000)
    },
    // 
    changeCity (item) {
      // 
      this.selCity = item
      // 
      this.postionList = this.cityPosition[this.selCity]
      // 
      this.currentIndex = 0
      // 
      this.$refs.mycity.toggle()
    },
    // 
    changeIndex (index) {
      this.currentIndex = index
    }
  },
  async created () {
    this.$toast({
      type: 'loading',
      message: '...',
      duration: 0
    })
    //  & 
    const res = await getQuestionDataApi()
    // 
    this.cityList = res.data.citys
    this.cityPosition = res.data.cityPositions
    // 
    this.selCity = this.cityList[''][0]
    // 
    this.postionList = this.cityPosition[this.selCity]
    // 
    this.totalCount = res.data.totalCount
    this.$toast.clear()
    // 
    this.myanimat()
  }
}
</script>

<style lang="less" scoped>
.question {
  background-color: #f7f4f5;
  height: 100vh;
  /deep/ .van-index-anchor {
    background-color: #ccc;
  }
  .box {
    span {
      font-size: 12px;
    }
  }
  .positionbox {
    display: flex;
    background-color: #fff;
    flex-wrap: nowrap;
    padding: 20px;
    overflow: auto;
    &::-webkit-scrollbar {
      display: none;
    }
    .tags {
      flex-shrink: 0; // 
      font-size: 12px;
      padding: 4px 6px;
      background-color: #ccc;
      border-radius: 20px;
      margin-right: 10px;
    }
    .active {
      background-color: red;
      color: #fff;
    }
  }
  .contentbox {
    position: relative;
    height: 200px;
    background-color: #fff;
    margin-top: 10px;
    .item {
      font-size: 12px;
      display: flex;
      flex-direction: column;
      text-align: center;
      position: absolute;
      i {
        width: 44px;
        height: 44px;
        border-radius: 50%;
        color: #fff;
        font-size: 24px;
        line-height: 44px;
      }
      span {
        margin-top: 5px;
      }
    }
    .center {
      position: absolute;
      top: 50%;
      left: 50%;
      width: 100px;
      height: 100px;
      transform: translate(-50%, -50%);
      background-image: url('../../../assets/@2x.png');
      background-size: 100%;
      .circle {
        padding: 10px;
        box-sizing: border-box;
      }
      .text {
        width: 80px;
        height: 80px;
        background-color: #f23a66;
        border-radius: 50%;
        color: #fff;
        .title {
          font-size: 14px;
          font-weight: 700;
          padding-top: 20px;
        }
        .num {
          font-size: 14px;
          padding-top: 10px;
        }
      }
    }
    .one {
      top: 17px;
      left: 42px;
      i {
        background-color: #ff5a5a;
      }
    }
    .two {
      top: 18px;
      right: 42px;
      i {
        background-color: #ffc403;
      }
    }
    .three {
      bottom: 17px;
      left: 42px;
      i {
        background-color: #00cde2;
      }
    }
    .four {
      bottom: 18px;
      right: 42px;
      i {
        background-color: #28d18c;
      }
    }
  }
  .total {
    margin-top: 10px;
    height: 130px;
    background-color: #fff;
    text-align: center;
    .text {
      font-size: 16px;
      padding-top: 20px;
    }
    .num {
      font-size: 24px;
      padding-top: 30px;
    }
  }
  .btn {
    margin: 15px;
    .mybtn {
      border-radius: 6px;
    }
  }
}
</style>
