<template>
  <div class="interview" v-if="currQue.detail">
    <!--  -->
    <van-nav-bar>
      <template #left>
        <i class="iconfont iconbtn_nav_back myicon" @click="back"></i>
      </template>
      <template #title>
        <span
          ><sup>{{ questionList.length }}</sup></span
        >
      </template>
      <template #right>
        <span @click="opencard"></span>
      </template>
    </van-nav-bar>
    <!--  -->
    <div class="box">
      <div class="title">
        {{ typeObj[currQue.detail.type] }}{{ currQue.detail.title }}
      </div>
      <div class="tags">
        <div
          class="tag"
          v-for="(item, index) in currQue.detail.tag"
          :key="index"
        >
          {{ item }}
        </div>
      </div>
      <!--  -->
      <div class="items" v-if="currQue.detail.type === 1">
        <div
          class="item"
          :class="{
            select: str[index] === singleAnswer,
            correct:
              step >= 2 &&
              currQue.answer &&
              currQue.answer.singleAnswer === str[index],
            error:
              step >= 2 &&
              singleAnswer !== currQue.answer.singleAnswer &&
              singleAnswer === str[index]
          }"
          v-for="(item, index) in currQue.detail.option"
          :key="index"
          @click="changeAnswer(str[index])"
        >
          {{ str[index] }}. {{ item }}
        </div>
      </div>
      <!--  -->
      <div class="items" v-if="currQue.detail.type === 2">
        <div
          class="item"
          :class="{
            select: multipleAnswer.includes(str[index]),
            correct:
              step >= 2 && currQue.answer.multipleAnswer.includes(str[index]),
            error:
              step >= 2 &&
              !currQue.answer.multipleAnswer.includes(str[index]) &&
              multipleAnswer.includes(str[index])
          }"
          v-for="(item, index) in currQue.detail.option"
          :key="index"
          @click="changeMutiAnswer(str[index])"
        >
          {{ str[index] }}. {{ item }}
        </div>
      </div>
    </div>
    <!--  -->
    <div class="answer" v-if="step >= 2">
      <h3></h3>
      <div class="rightAns">
        {{
          currQue.answer.singleAnswer || currQue.answer.multipleAnswer
        }}
      </div>
      <div class="msg">
        <span>{{ diffObj[currQue.answer.difficulty] }}</span>
        <span> {{ currQue.answer.submitNum }}</span>
        <span> {{ currQue.answer.correctNum }}</span>
      </div>
      <div class="info">
        {{ currQue.answer.answerAnalysis }}
      </div>
    </div>
    <!--  -->
    <div class="foot">
      <div class="one">
        <i class="iconfont iconbtn_shoucang_nor"></i><span></span>
      </div>
      <div class="one">
        <i class="iconfont iconbtn_shuati_fankui"></i><span></span>
      </div>
      <div class="two">
        <span class="mynum">{{ currIndex + 1 }}</span
        >/{{ questionList.length }}
      </div>
      <div class="two">
        <van-button @click="nextone" type="info" v-if="step >= 2 && !isFinished"
          ></van-button
        >
        <van-button
          @click="submit"
          type="danger"
          v-if="step < 2 && !isFinished"
          :disabled="step !== 1"
          ></van-button
        >
        <van-button @click="jiaojuan" type="danger" v-if="isFinished"
          ></van-button
        >
      </div>
    </div>
    <!--  -->
    <van-popup class="answercard" v-model="cardShow" position="bottom">
      <van-nav-bar title="">
        <template #right
          ><span>{{ currIndex + 1 }}</span
          >/{{ questionList.length }}
        </template>
      </van-nav-bar>
      <div class="total">{{ questionList.length }}</div>
      <ul class="items">
        <li
          class="item"
          :class="{
            current: currIndex === index,
            error:
              questionList[index].answer && !questionList[index].answer.isRight,
            success:
              questionList[index].answer && questionList[index].answer.isRight
          }"
          v-for="(item, index) in questionList.length"
          :key="index"
          @click="changeIndex(index)"
        >
          {{ item }}
        </li>
      </ul>
    </van-popup>
  </div>
</template>

<script>
import {
  getQuestionListApi,
  submitQuestionApi,
  getNextoneApi
} from '@/api/question'
export default {
  data () {
    return {
      // 
      //  0
      //  1
      //  2
      //  3: 
      step: 0,
      // 
      city: '',
      // 
      type: '',
      // 
      questionList: [],
      // 
      currIndex: 0,
      // 
      typeObj: {
        1: '',
        2: '',
        3: ''
      },
      // 
      diffObj: {
        1: '',
        2: '',
        3: ''
      },
      // 
      str: 'ABCDEFGHIJKL',
      // 
      singleAnswer: '',
      // 
      multipleAnswer: [],
      // 
      cardShow: false
    }
  },
  computed: {
    // 
    currQue () {
      return this.questionList[this.currIndex] || {}
    },
    isFinished () {
      //  answer 
      return this.questionList.every(item => {
        return !!item.answer
      })
    }
  },
  methods: {
    // 
    jiaojuan () {
      this.$dialog
        .confirm({
          title: '',
          message: ''
        })
        .then(() => {
          this.$toast.success('')
          this.$router.push('/question')
        })
        .catch()
    },
    // 
    async changeIndex (index) {
      this.currIndex = index
      // 
      this.cardShow = false
      if (this.currQue.answer) {
        this.step = 2
      } else if (this.currQue.detail) {
        this.step = 0
      } else {
        //  currIndex 
        const res = await getNextoneApi(this.currQue.id)
        // 
        this.$set(this.currQue, 'detail', res.data)
        //  0
        this.step = 0
      }
    },
    // 
    opencard () {
      this.cardShow = true
    },
    // 
    changeMutiAnswer (ans) {
      //  ans  multipleAnswer  
      //  
      //  
      //  ans  multipleAnswer 
      const index = this.multipleAnswer.indexOf(ans)
      if (this.step < 2) {
        if (index === -1) {
          // ans  multipleAnswer 
          this.multipleAnswer.push(ans)
        } else {
          // ans  multipleAnswer 
          this.multipleAnswer.splice(index, 1)
        }
        //  1
        this.step = 1
      }
    },
    // 
    async nextone () {
      //  + 1
      this.currIndex += 1
      if (this.currQue.answer) {
        this.step = 2
      } else if (this.currQue.detail) {
        this.step = 0
      } else {
        // every
        this.step = 0
        // 
        this.singleAnswer = ''
        this.multipleAnswer = []
        // 
        const res = await getNextoneApi(this.currQue.id)
        // 
        // this.currQue.detail = res.data
        this.$set(this.currQue, 'detail', res.data)
      }
    },
    // 
    async submit () {
      // 
      const res = await submitQuestionApi({
        id: this.currQue.id,
        singleAnswer: this.singleAnswer,
        multipleAnswer: this.multipleAnswer
      })
      console.log(res.data)
      //  detail 
      // this.currQue.answer = res.data
      this.$set(this.currQue, 'answer', res.data)
      //  2
      this.step = 2
    },
    // 
    changeAnswer (ans) {
      // step < 2 
      if (this.step < 2) {
        this.singleAnswer = ans
        // 1
        this.step = 1
      }
    },
    back () {}
  },
  async created () {
    //  & 
    this.city = this.$route.params.city
    this.type = this.$route.params.type
    // 
    const res = await getQuestionListApi({
      type: this.type,
      city: this.city
    })
    this.questionList = res.data
  }
}
</script>

<style lang="less" scoped>
.interview {
  margin-bottom: 80px;
  .myicon {
    font-size: 40px;
    color: #191b3a;
  }
  /deep/ .van-nav-bar__left {
    padding: 0;
  }
  .box {
    padding: 30px 15px;
    .title {
      font-size: 16px;
      line-height: 24px;
    }
    .tags {
      display: flex;
      flex-wrap: wrap;
      font-size: 12px;
      padding: 20px 0;
      .tag {
        padding: 4px 8px;
        background: #f7f4f5;
        margin-right: 20px;
      }
    }
    .items {
      font-size: 16px;
      .item {
        border: 1px solid #f7f4f5;
        padding: 10px 15px;
        border-radius: 6px;
        margin-bottom: 20px;
      }
      .select {
        border: 1px solid #ccc;
      }
      .correct {
        background-color: #ddfad9;
        color: #42c779;
        position: relative;
        &::after {
          position: absolute;
          top: 50%;
          right: 10px;
          transform: translateY(-50%);
          font-family: 'iconfont' !important;
          font-size: 16px;
          font-style: normal;
          -webkit-font-smoothing: antialiased;
          -moz-osx-font-smoothing: grayscale;
          content: '\e604';
        }
      }
      .error {
        background-color: #ffefea;
        color: #ec5851;
        position: relative;
        &::after {
          position: absolute;
          top: 50%;
          right: 10px;
          transform: translateY(-50%);
          font-family: 'iconfont' !important;
          font-size: 16px;
          font-style: normal;
          -webkit-font-smoothing: antialiased;
          -moz-osx-font-smoothing: grayscale;
          content: '\e605';
        }
      }
    }
  }
  .answer {
    padding: 0 15px;
    h3 {
      font-size: 18px;
    }
    .rightAns {
      font-size: 16px;
      color: #1dc779;
      line-height: 50px;
    }
    .msg {
      font-size: 12px;
      background-color: #f7f4f5;
      padding: 8px;
      display: flex;
      justify-content: space-between;
    }
    .info {
      font-size: 14px;
      padding: 20px 0;
    }
  }
  .foot {
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    box-sizing: border-box;
    display: flex;
    align-items: center;
    padding: 15px 15px;
    background-color: #fff;
    .one {
      flex: 1;
      font-size: 12px;
      display: flex;
      flex-direction: column;
      text-align: center;
      i {
        font-size: 26px;
      }
    }
    .two {
      flex: 4;
      text-align: center;
      font-size: 14px;
      .mynum {
        font-size: 20px;
        color: red;
      }
    }
  }
  .answercard {
    .total {
      background-color: #f7f4f5;
      padding: 15px;
      font-size: 14px;
      color: #b4b4bd;
    }
    .items {
      display: flex;
      flex-wrap: wrap;
      font-size: 12px;
      color: #b4b4bd;
      padding: 20px;
      .item {
        width: 33px;
        height: 33px;
        border-radius: 50%;
        border: 1px solid #b4b4bd;
        line-height: 33px;
        text-align: center;
        margin: 0 12px 10px 0;
      }
      .current {
        border: 1px solid #666;
        color: #666;
      }
      .error {
        border: 1px solid transparent;
        color: #ff6d49;
        background-color: #ffefea;
      }
      .success {
        border: 1px solid transparent;
        color: #1dc779;
        background-color: #ddfad9;
      }
    }
  }
}
</style>
