import Vue from 'vue'
import Vuex from 'vuex'
// 
import { getUserApi } from '../api/login.js'

Vue.use(Vuex)

export default new Vuex.Store({
  // 
  state: {
    // 
    user: '',
    // 
    isLogin: false,
    // 
    genderObj: {
      0: '',
      1: '',
      2: ''
    }
  },
  //  state 
  getters: {
    // 
    getState (state) {
      return function (id, prop) {
        return state.user[prop] && state.user[prop].includes(+id)
      }
    },
    // getters 
    // 
    collectState (state) {
      return function (id) {
        return (
          state.user.collectArticles && state.user.collectArticles.includes(+id)
        )
      }
    },
    // 
    starState (state) {
      return function (id) {
        return state.user.starArticles && state.user.starArticles.includes(+id)
      }
    }
  },
  // 
  mutations: {
    //  user 
    setUser (state, payload) {
      state.user = payload
    },
    //  isLogin 
    setIsLogin (state, payload) {
      state.isLogin = payload
    }
  },
  actions: {
    //  user 
    async setUser (context) {
      // 1.0 
      const res = await getUserApi()
      // 2.0  mutations
      context.commit('setUser', res.data)
    }
  },
  modules: {}
})
