<template>
  <div ref="mydetail" class="shareDetail" v-if="detailObj && detailObj.author">
    <!--  -->
    <hmnavbar @back="$router.back()"></hmnavbar>
    <!--  -->
    <h3 class="title">
      {{ detailObj.title }}
    </h3>
    <!--  -->
    <div class="author">
      <img class="avatar" :src="baseURL + detailObj.author.avatar" alt="" />
      <div class="info">
        <div class="nickname">{{ detailObj.author.nickname }}</div>
        <div class="time">{{ detailObj.created_at | hmtimefilter }}</div>
      </div>
    </div>
    <!--  -->
    <div class="content" v-html="detailObj.content"></div>
    <!--  -->
    <div class="comment">
      <h3><sup>87</sup></h3>
      <div class="items">
        <van-list
          @load="onload"
          v-model="loading"
          finished-text=""
          :finished="finished"
        >
          <comment
            @imgclick="openReplayComment"
            :item="item"
            v-for="(item, index) in commentList"
            :key="index"
          ></comment>
        </van-list>
      </div>
    </div>
    <!--  -->
    <div class="write">
      <div class="input" @click="opencomment">
        
      </div>
      <div class="part">
        <i
          @click="collectArt"
          class="iconfont iconbtn_shoucang_nor"
          :class="{
            active: getState(id, 'collectArticles')
          }"
        ></i
        ><span>{{ detailObj.collect }}</span>
      </div>
      <div class="part">
        <i
          @click="starArt"
          class="iconfont iconbtn_dianzan_small_nor"
          :class="{
            active: getState(id, 'starArticles')
          }"
        ></i
        ><span>{{ detailObj.star }}</span>
      </div>
      <div class="part">
        <i @click="openShare" class="iconfont iconbtn_share"></i
        ><span>322</span>
      </div>
    </div>
    <!--  -->
    <van-popup v-model="commentShow" position="bottom" class="commentpanel">
      <textarea class="ipt" :placeholder="myplace" v-model="content"></textarea>
      <div class="btn">
        <span v-if="content.trim().length === 0" style="color: #ccc"></span>
        <span @click="sendComment" v-else style="color: red"></span>
      </div>
    </van-popup>
    <!--  -->
    <van-popup class="sharepanel" v-model="shareShow">
      <div class="top"></div>
      <div class="box">
        <div class="img">
          <img :src="pageImage" alt="" />
        </div>
        <div class="logo">
          <img src="../../../assets/img_share_logo@2x.png" alt="" />
        </div>
        <div class="ewm">
          <div id="qrcode" ref="qrcode"></div>
        </div>
      </div>
    </van-popup>
  </div>
</template>

<script>
//  api
import {
  getShareDetail,
  getCommentApi,
  sendCommentApi,
  collectArtApi,
  starArtApi
} from '@/api/articles'
// 
import comment from './comment'
import { getLocal } from '@/utils/local.js'
// 
import { mapState, mapGetters } from 'vuex'
// 
import html2canvas from 'html2canvas'
// 
import QRCode from 'qrcodejs2'
export default {
  data () {
    return {
      //  id
      id: '',
      // 
      detailObj: {},
      // 
      baseURL: process.env.VUE_APP_URL,
      // list 
      loading: false,
      // list 
      finished: false,
      // 
      limit: 5,
      // 
      start: -5,
      // 
      commentList: [],
      // 
      total: 0,
      // 
      commentShow: false,
      // 
      content: '',
      // 
      isReplay: false,
      // 
      myplace: '',
      // 
      currentComment: {},
      // 
      shareShow: false,
      // 
      pageImage: ''
    }
  },
  computed: {
    ...mapState(['user', 'isLogin']),
    ...mapGetters(['collectState', 'starState', 'getState'])
    // // 
    // collectState () {
    //   return (
    //     this.user.collectArticles &&
    //     this.user.collectArticles.includes(+this.id)
    //   )
    // },
    // // 
    // starState () {
    //   return this.user.starArticles && this.user.starArticles.includes(+this.id)
    // }
  },
  methods: {
    openShare () {
      // 
      this.shareShow = true
      // 
      html2canvas(window.document.body, {
        // 
        useCORS: true
      }).then(canvas => {
        //  canvas 
        this.pageImage = canvas.toDataURL('image/png')
      })
      this.$nextTick(() => {
        // 
        this.$refs.qrcode.innerHTML = ''
        // 
        const qrcode = new QRCode('qrcode', {
          width: 75,
          height: 75,
          text: window.location.href
        })
        console.log(qrcode)
      })
    },
    //  & 
    async starArt () {
      if (!this.$login()) {
        return
      }
      this.$toast({
        type: 'loading',
        message: '...',
        duration: 0
      })
      //  & 
      await starArtApi(this.id)
      // 
      if (this.user.starArticles && this.user.starArticles.includes(+this.id)) {
        this.$toast.success('')
        this.detailObj.star -= 1
      } else {
        this.$toast.success('')
        this.detailObj.star += 1
      }
      // 
      this.$store.dispatch('setUser')
    },
    // 
    async collectArt () {
      //  user 
      if (!this.isLogin) {
        //  user 
        //  token
        const token = getLocal('token')
        if (token) {
          //  token 
          this.$store.dispatch('setUser')
          //  true
          this.$store.commit('setIsLogin', true)
        } else {
          // 
          this.$toast.fail('')
          // 
          this.$router.push('/login?_redirect=' + this.$route.fullPath)
          return
        }
      }
      // 
      this.$toast({
        type: 'loading',
        message: '...',
        duration: 0
      })
      //  & 
      await collectArtApi(this.id)
      // 
      if (
        this.user.collectArticles &&
        this.user.collectArticles.includes(+this.id)
      ) {
        // 
        //   - 1
        this.$toast.success('')
        this.detailObj.collect -= 1
      } else {
        // 
        //  ,  + 1
        this.$toast.success('')
        this.detailObj.collect += 1
      }
      // 
      this.$store.dispatch('setUser')
    },
    // 
    openReplayComment (item) {
      this.commentShow = true
      // : true
      this.isReplay = true
      this.myplace = `${item.author.nickname}`
      // 
      this.currentComment = item
    },
    // 
    // 
    async sendComment () {
      //  token
      //  token
      const token = getLocal('token')
      if (!token) {
        // 
        this.$toast.fail('')
        // 
        this.$router.push(`/login?_redirect=${this.$route.fullPath}`)
        return // 
      }
      if (this.isReplay === false) {
        // 
        // 
        const res = await sendCommentApi({
          content: this.content,
          article: this.id
        })
        // 
        const returnObj = res.data
        // 
        returnObj.star = 0
        // 
        this.commentList.unshift(returnObj)
      } else {
        // 
        const res = await sendCommentApi({
          content: this.content, // 
          article: this.id, //  id
          parent: this.currentComment.id //  id
        })
        // 
        const replayObj = res.data
        // 
        this.currentComment.children_comments.push(replayObj)
      }
      // 
      this.content = ''
      // 
      this.commentShow = false
    },
    // 
    opencomment () {
      this.commentShow = true
      //  false
      this.isReplay = false
      this.myplace = ''
    },
    // 1.0 
    // 2.0 list  list 
    async onload () {
      // 
      this.start += this.limit
      // 
      const res = await getCommentApi({
        id: this.id,
        limit: this.limit,
        start: this.start
      })
      // 
      this.commentList.push(...res.data.list)
      this.total = res.data.total
      //  LIST 
      this.loading = false
      // 
      if (this.commentList.length >= this.total) {
        this.finished = true
      }
    }
  },
  async created () {
    //  id
    this.id = this.$route.params.id
    try {
      //  id 
      const res = await getShareDetail(this.id)
      this.detailObj = res.data
    } catch {}
  },
  components: {
    comment
  }
}
</script>

<style lang="less" scoped>
.active {
  color: red;
}
.shareDetail {
  padding-bottom: 80px;
  .title {
    font-size: 18px;
    color: #181a39;
    line-height: 25px;
    padding: 20px 15px;
  }
  .author {
    padding: 0 15px;
    display: flex;
    .avatar {
      width: 35px;
      height: 35px;
      border-radius: 50%;
      margin-right: 10px;
    }
    .info {
      font-size: 12px;
      .nickname {
        color: #545671;
        font-weight: 700;
        line-height: 17px;
      }
      .time {
        color: #b4b4bd;
        line-height: 17px;
      }
    }
  }
  .content {
    padding: 20px 15px;
    font-size: 16px;
    /deep/ img {
      width: 100%;
    }
  }
  .comment {
    font-size: 18px;
    color: #222222;
    line-height: 25px;
    padding: 20px 15px;
    h3 {
      sup {
        font-weight: 400;
        color: #bfbfc7;
        margin-left: 4px;
      }
    }
    .items {
    }
  }
  .write {
    background: #fff;
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    padding: 15px;
    display: flex;
    font-size: 12px;
    align-items: center;
    box-sizing: border-box;
    .input {
      background-color: #f7f4f5;
      width: 190px;
      height: 34px;
      line-height: 34px;
      padding-left: 8px;
      box-sizing: border-box;
      color: #c6c5cc;
    }
    .part {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      flex: 1;
      color: #c9c9d0;
      i {
        font-size: 30px;
      }
      span {
      }
    }
  }
  .commentpanel {
    padding: 20px 15px;
    box-sizing: border-box;
    .ipt {
      box-sizing: border-box;
      padding: 15px;
      width: 100%;
      height: 100px;
      border: none;
      background: #f7f4f5;
      font-size: 14px;
    }
    .btn {
      margin-top: 10px;
      font-size: 16px;
      text-align: right;
    }
  }
  .sharepanel {
    width: 90%;
    background-color: #00b8d4;
    padding: 0 15px 15px;
    box-sizing: border-box;
    .top {
      color: #fff;
      font-size: 12px;
      text-align: center;
      height: 40px;
      line-height: 40px;
      // 
      letter-spacing: 5px;
    }
    .box {
      background-color: #fff;
      border-radius: 10px;
      overflow: hidden;
      .img {
        height: 350px;
        overflow: hidden;
        img {
          width: 100%;
        }
      }
      .logo {
        text-align: center;
        img {
          width: 120px;
          height: 36px;
        }
      }
      .ewm {
        #qrcode {
          /deep/ img {
            margin: 0 auto;
          }
        }
      }
    }
  }
}
</style>
