// 
//  request
import request from '../utils/request.js'
// 

// 
export function getCodeApi (data) {
  //  .then
  return request({
    url: '/au/code',
    method: 'POST',
    data: data
  })
}

// 
export function loginApi (data) {
  return request({
    url: '/au/login',
    method: 'POST',
    data: data
  })
}

//  token 
export function getUserApi () {
  return request({
    url: '/au/info',
    needToken: true //  token
  })
}

// 
export function editUserApi (data) {
  return request({
    url: '/au/edit',
    method: 'POST',
    needToken: true,
    data: data
  })
}

// 
export function uploadApi (file) {
  // console.log(file)
  //  formData 
  var fd = new FormData()
  fd.append('files', file)
  return request({
    url: '/upload',
    method: 'POST',
    needToken: true,
    data: fd
  })
}
