<template>
  <div class="info">
    <!--  -->
    <hmnavbar @back="back" title=""></hmnavbar>
    <div class="userInfo">
      <!--  -->
      <van-cell @click="toEdit('avatar')" title="" is-link>
        <template #default>
          <div class="avatar">
            <img :src="baseURL + user.avatar" alt="" />
          </div>
        </template>
      </van-cell>
      <van-cell-group class="group">
        <van-cell
          @click="toEdit('nickname')"
          title=""
          :value="user.nickname"
          is-link
        ></van-cell>
        <van-cell
          title=""
          :value="genderObj[user.gender]"
          is-link
          @click="openGender"
        ></van-cell>
        <van-cell
          title=""
          :value="areaList.city_list[user.area]"
          is-link
          @click="openArea"
        ></van-cell>
        <van-cell
          @click="toEdit('intro')"
          title=""
          :value="user.intro"
          is-link
        ></van-cell>
      </van-cell-group>
      <!--  -->
      <van-button @click="logout" class="btn" color="#ea3a64" plain size="large"
        ></van-button
      >
    </div>
    <!--  -->
    <van-popup v-model="gendershow" position="bottom">
      <!--  picker  -->
      <van-picker
        v-if="pickerShow"
        show-toolbar
        :columns="columns"
        :default-index="user.gender"
        @confirm="genderConfirm"
        @cancel="genderCancel"
      />
    </van-popup>
    <!--  -->
    <van-popup v-model="areashow" position="bottom">
      <!--  -->
      <van-area
        title=""
        :area-list="areaList"
        :value="user.area"
        :columns-num="2"
        @confirm="areaConfirm"
      />
    </van-popup>
  </div>
</template>

<script>
// 
import { mapState, mapMutations, mapActions } from 'vuex'
// 
import { areaList } from '@vant/area-data'
//  localstorage 
import { removeLocal } from '../../../utils/local'
//  api
import { editUserApi } from '../../../api/login.js'
export default {
  data () {
    return {
      baseURL: process.env.VUE_APP_URL,
      // 
      areaList: areaList,
      // 
      gendershow: false,
      //  picker 
      columns: ['', '', ''],
      // 
      areashow: false,
      // 
      pickerShow: true
    }
  },
  computed: {
    ...mapState(['user', 'genderObj'])
  },
  methods: {
    ...mapMutations(['setIsLogin']),
    ...mapActions(['setUser']),
    //  editInfo 
    toEdit (prop) {
      this.$router.push(`/editInfo?prop=${prop}`)
    },
    // 
    async areaConfirm (arr) {
      this.$toast({
        type: 'loading',
        message: '...',
        duration: 0
      })
      // 
      console.log(arr[1].code)
      // 
      await editUserApi({
        area: arr[1].code
      })
      // 
      this.areashow = false
      // 
      this.setUser()
      this.$toast.clear()
    },
    // 
    openArea () {
      this.areashow = true
    },
    // 
    genderCancel () {
      console.log('')
      this.gendershow = false
    },
    // 
    async genderConfirm (value, index) {
      // 
      this.$toast({
        type: 'loading',
        message: '...',
        duration: 0
      })
      // : index
      // 
      await editUserApi({
        gender: index
      })
      // 
      this.gendershow = false
      // //  vuex 
      // const res = await getUserApi()
      // //  vuex 
      // this.$store.commit('setUser', res.data)
      this.$store.dispatch('setUser')
      // 
      this.$toast.clear()
    },
    // 
    openGender () {
      this.gendershow = true
      //  picker
      this.pickerShow = false
      setTimeout(() => {
        this.pickerShow = true
      }, 100)
    },
    // 
    logout () {
      this.$dialog
        .confirm({
          title: '',
          message: ''
        })
        .then(() => {
          //  token
          removeLocal('token')
          //  isLogin  false
          this.setIsLogin(false)
          // 
          this.$router.push('/login')
        })
        .catch(() => {
          console.log('')
        })
    },
    back () {
      // 
      this.$router.push('/my')
    }
  }
}
</script>

<style lang="less" scoped>
.info {
  height: 100vh;
  background-color: #f7f4f5;
  .userInfo {
    margin-top: 18px;
    padding: 0 15px;
    /deep/ .van-cell.van-cell--clickable {
      align-items: center;
      border-radius: 6px;
    }
    .avatar {
      display: inline-block;
      vertical-align: bottom;
      width: 40px;
      height: 40px;
      img {
        width: 40px;
        height: 40px;
        border-radius: 50%;
      }
    }
    .group {
      margin-top: 10px;
      border-radius: 6px;
    }
    .btn {
      margin-top: 10px;
      border: none;
      font-weight: 700;
      border-radius: 6px;
    }
  }
}
</style>
