<template>
  <div class="login">
    <!--  -->
    <hmnavbar @back="back"></hmnavbar>
    <!--  -->
    <div class="form">
      <h3></h3>
      <van-form @submit="login" ref="myform">
        <van-field
          v-model="user.phone"
          placeholder=""
          :rules="rules.phone"
          name="phone"
        >
          <template #left-icon>
            <i class="iconfont iconbianzu1 formicon"></i>
          </template>
        </van-field>
        <van-field
          v-model="user.code"
          placeholder=""
          :rules="rules.code"
        >
          <template #left-icon>
            <i class="iconfont iconyanzhengma formicon"></i>
          </template>
          <template #button>
            <span v-if="totalTime === 6" @click="getCode" class="mycolor"
              ></span
            >
            <span v-else>{{ totalTime }}s </span>
          </template>
        </van-field>
        <div class="xieyi">
          <span class="mycolor"></span><span
            class="mycolor"
            ></span
          >
        </div>
        <div class="btn">
          <van-button class="sure" size="large" color="#e40137"
            ></van-button
          >
        </div>
      </van-form>
    </div>
  </div>
</template>

<script>
// 
import { getCodeApi, loginApi } from '../../api/login.js'
//  localstroage 
import { setLocal } from '../../utils/local'
// 
// import hmnavbar from '../../components/hmnavbar'
export default {
  data () {
    return {
      // 
      user: {
        phone: '18888881111',
        code: ''
      },
      // 
      rules: {
        phone: [
          { required: true, message: '', trigger: 'onBlur' },
          {
            pattern: /^(0|86|17951)?(13[0-9]|15[012356789]|166|17[3678]|18[0-9]|14[57])[0-9]{8}$/,
            message: '',
            trigger: 'onBlur'
          }
        ],
        code: [
          { required: true, message: '', trigger: 'onBlur' },
          {
            pattern: /^\d{4}$/,
            message: '4',
            trigger: 'onBlur'
          }
        ]
      },
      // 
      baseURL: process.env.VUE_APP_URL,
      // 
      totalTime: 6
    }
  },
  methods: {
    back () {
      // 
      this.$router.push('/find')
    },
    // 
    getCode () {
      // 1.0 
      this.$refs.myform.validate(['phone']).then(async () => {
        // 
        const interval = setInterval(() => {
          this.totalTime--
          if (this.totalTime <= 0) {
            // 
            clearInterval(interval)
            // 
            this.totalTime = 6
          }
        }, 1000)
        // 
        this.$toast({
          type: 'loading',
          message: '...',
          duration: 0
        })
        // 
        try {
          // 2.0 
          const res = await getCodeApi({
            mobile: this.user.phone
          })
          // 
          this.$toast(res.data)
          // 
          this.user.code = res.data
        } catch {
          console.log('')
        }
      })
    },
    // 
    async login () {
      // 
      this.$toast({
        type: 'loading',
        message: '...',
        duration: 0
      })
      try {
        // 
        const res = await loginApi({
          mobile: this.user.phone,
          code: this.user.code
        })
        // 
        //  jwt token
        //  user
        // 
        // 2.0  token  localStorage 
        setLocal('token', res.data.jwt)
        // 3.0 ( vuex )
        this.$store.commit('setUser', res.data.user)
        // 
        this.$toast.clear()
        // 4.0  true
        this.$store.commit('setIsLogin', true)
        //  _redirect
        // console.log(this.$route.query._redirect)
        const redirect = this.$route.query._redirect
        if (redirect) {
          // 
          this.$router.push(redirect)
        } else {
          //  my 
          this.$router.push('/my')
        }
      } catch {
        console.log('')
      }
    }
  }
  // components: {
  //   hmnavbar
  // }
}
</script>

<style lang="less" scoped>
.login {
  .form {
    padding: 0 15px;
    h3 {
      font-size: 18px;
      padding: 50px 0;
    }
    .formicon {
      margin-right: 15px;
    }
    .mycolor {
      color: #00b8d4;
    }
    .xieyi {
      font-size: 12px;
      margin-top: 15px;
    }
    .btn {
      margin-top: 44px;
      .sure {
        border-radius: 30px;
      }
    }
  }
}
</style>
