<template>
  <div class="my" v-if="user && user.nickname">
    <!--  -->
    <div class="userInfo">
      <div class="user">
        <div class="left">
          <div class="name">{{ user.nickname }}</div>
          <div class="mark">{{ user.intro }}</div>
        </div>
        <div class="right" @click="toInfo">
          <img :src="baseURL + user.avatar" alt="" />
        </div>
      </div>
      <div class="data">
        <div class="item">
          <div class="num">{{ user.submitNum }}</div>
          <div class="title"></div>
        </div>
        <div class="item">
          <div class="num">{{ user.collectQuestions.length }}</div>
          <div class="title"></div>
        </div>
        <div class="item">
          <div class="num">{{ user.errorQuestions.length }}</div>
          <div class="title"></div>
        </div>
        <div class="item">
          <div class="num">
            {{
              Math.round(
                ((user.submitNum - user.errorNum) / user.submitNum) * 100
              ) + '%'
            }}
          </div>
          <div class="title"></div>
        </div>
      </div>
      <div class="position">
        <van-cell
          @click="toEdit"
          class="mycell"
          title=""
          :value="user.position"
          is-link
        >
          <template #icon>
            <i class="iconfont iconicon_mine_gangwei mypos"></i>
          </template>
        </van-cell>
      </div>
    </div>
    <!--  -->
    <div class="other">
      <div class="face">
        <h3></h3>
        <div class="items">
          <div class="item">
            <div class="top">
              <span>+{{ user.shareData.read.yesterday }}</span>
            </div>
            <div class="center">
              {{ user.shareData.read.total }}
            </div>
            <div class="bottom"></div>
          </div>
          <div class="item">
            <div class="top">
              <span>+{{ user.shareData.star.yesterday }}</span>
            </div>
            <div class="center">
              {{ user.shareData.star.total }}
            </div>
            <div class="bottom"></div>
          </div>
          <div class="item">
            <div class="top">
              <span>+{{ user.shareData.comment.yesterday }}</span>
            </div>
            <div class="center">
              {{ user.shareData.comment.total }}
            </div>
            <div class="bottom"></div>
          </div>
        </div>
      </div>
      <div class="itembox">
        <van-cell class="mycell" title="" value="21" is-link>
          <template #icon>
            <i class="iconfont iconicon_mine_gangwei mypos"></i>
          </template>
        </van-cell>
        <van-cell class="mycell" title="" value="21" is-link>
          <template #icon>
            <i class="iconfont iconicon_mine_gangwei mypos"></i>
          </template>
        </van-cell>
        <van-cell class="mycell" title="" value="21" is-link>
          <template #icon>
            <i class="iconfont iconicon_mine_gangwei mypos"></i>
          </template>
        </van-cell>
        <van-cell class="mycell" title="" value="21" is-link>
          <template #icon>
            <i class="iconfont iconicon_mine_gangwei mypos"></i>
          </template>
        </van-cell>
        <van-cell class="mycell" title="" value="21" is-link>
          <template #icon>
            <i class="iconfont iconicon_mine_gangwei mypos"></i>
          </template>
        </van-cell>
        <van-cell class="mycell" title="" value="21" is-link>
          <template #icon>
            <i class="iconfont iconicon_mine_gangwei mypos"></i>
          </template>
        </van-cell>
      </div>
    </div>
  </div>
</template>

<script>
// 
import { mapState } from 'vuex'
export default {
  data () {
    return {
      baseURL: process.env.VUE_APP_URL
    }
  },
  methods: {
    // 
    toEdit () {
      this.$router.push('/editInfo?prop=position')
    },
    toInfo () {
      // 
      this.$router.push('/info')
    }
  },
  //  store  state  user 
  computed: {
    //  state  user  vuex  .vue 
    ...mapState(['user'])
  }
}
</script>

<style lang="less" scoped>
.my {
  background-color: #f7f4f5;
  padding-bottom: 70px;
  .userInfo {
    height: 216px;
    background: linear-gradient(45deg, #ce0031, #b8002c);
    font-size: 12px;
    padding: 0 15px;
    color: #fff;
    .user {
      padding-top: 30px;
      display: flex;
      justify-content: space-between;
      .left {
        .name {
          font-size: 21px;
        }
        .mark {
          margin-top: 10px;
          opacity: 0.7;
        }
      }
      .right {
        img {
          width: 50px;
          height: 50px;
          opacity: 1;
          border: 3px solid rgba(255, 255, 255, 0.38);
          border-radius: 50%;
        }
      }
    }
    .data {
      margin-top: 30px;
      display: flex;
      .item {
        flex: 1;
        text-align: center;
        .num {
          font-size: 21px;
        }
        .title {
          margin-top: 8px;
          opacity: 0.7;
        }
      }
    }
    .position {
      margin-top: 20px;
      .mycell {
        border-radius: 6px;
      }
      .mypos {
        margin-right: 10px;
      }
    }
  }
  .other {
    margin-top: 25px;
    padding: 0 15px;
    .face {
      background-color: #fff;
      padding: 0 15px 16px;
      border-radius: 6px;
      h3 {
        font-size: 14px;
        padding: 18px 0;
      }
      .items {
        font-size: 12px;
        display: flex;
        .item {
          flex: 1;
          text-align: center;
          .top {
            color: #b4b4bd;
            span {
              color: #2cc4db;
            }
          }
          .center {
            padding: 10px 0 5px;
            font-weight: 700;
            font-size: 21px;
          }
          .bottom {
          }
        }
      }
    }
    .itembox {
      margin-top: 10px;
      border-radius: 6px;
      overflow: hidden;
      .mypos {
        margin-right: 8px;
      }
    }
  }
}
</style>
