<template>
  <div class="usevant">
    <h2> vant</h2>
    <h3>0.0button</h3>
    <div class="btn"><button></button><br /></div>
    <div></div>
    <i class="iconfont iconbtn_nav_back"></i>
    <i class="iconfont iconbianzu1"></i>
    <i class="iconfont iconyanzhengma"></i>
    <h3>1.0  vant </h3>
    <van-button type="primary"></van-button>
    <van-button type="info"></van-button>
    <van-button type="default"></van-button>
    <van-button type="warning"></van-button>
    <van-button type="danger"></van-button>
    <h3>2.0 vant </h3>
    <van-icon name="like-o" />
    <van-icon name="gem-o" />
    <h3>3.0  vant </h3>
    <van-cell-group>
      <van-cell title="" value="" />
      <van-cell title="" value="" label=""></van-cell>
    </van-cell-group>
    <h3>4.0  vant </h3>
    <van-cell>
      <template #title>
        <van-icon name="gem-o" /><van-icon name="gem-o" />
      </template>
      <template #default>
        <van-icon name="gem-o" /><van-icon name="gem-o" />
      </template>
    </van-cell>
    <h3>5.0  vant  navbar</h3>
    <van-nav-bar>
      <template #left>
        <i class="iconfont iconbtn_nav_back myarrow05"></i>
      </template>
    </van-nav-bar>
    <h3>6.0  vant  field</h3>
    <div></div>
    <van-field
      type="text"
      v-model="value"
      label=""
      placeholder=""
    />
    <div></div>
    <van-field
      type="tel"
      v-model="value"
      label=""
      placeholder=""
    />
    <div></div>
    <van-field
      type="digit"
      v-model="value"
      label=""
      placeholder=""
    />
    <div></div>
    <van-field
      type="number"
      v-model="value"
      label=""
      placeholder=""
    />
    <div></div>
    <van-field
      type="password"
      v-model="value"
      label=""
      placeholder=""
    />
    <h3>7.0  vant  form</h3>
    <h4></h4>
    <van-form>
      <van-field
        label=""
        placeholder=""
        v-model="use.phone"
        :rules="rules.phone"
      ></van-field>
    </van-form>
    <h4>:</h4>
    <van-form ref="myglobform">
      <van-field
        label=""
        placeholder=""
        v-model="use.phone"
        :rules="rules.phone"
      ></van-field>
      <van-field
        label=""
        placeholder=""
        v-model="use.code"
        :rules="rules.code"
      ></van-field>
      <div>
        <a @click="login" href="javascript:void(0)"></a>
      </div>
    </van-form>
    <h4>:</h4>
    <van-form @submit="event">
      <van-field
        label=""
        placeholder=""
        v-model="use.phone"
        :rules="rules.phone"
      ></van-field>
      <van-field
        label=""
        placeholder=""
        v-model="use.code"
        :rules="rules.code"
      ></van-field>
      <van-button></van-button>
    </van-form>
    <h4></h4>
    <van-form ref="myform">
      <van-field
        label=""
        placeholder=""
        v-model="use.phone"
        :rules="rules.phone"
        name="phone"
      ></van-field>
      <van-field
        label=""
        placeholder=""
        v-model="use.code"
        :rules="rules.code"
      >
        <template #button>
          <span @click="checkphone"></span>
        </template>
      </van-field>
    </van-form>
    <h3>8.0 vant  toast</h3>
    <van-button @click="toast1"></van-button>
    <van-button @click="toast2"></van-button>
    <van-button @click="toast3"></van-button>
    <van-button @click="toast4"></van-button>
    <van-button @click="toast5"></van-button>
    <van-button @click="clear"></van-button>
  </div>
</template>

<script>
export default {
  data () {
    return {
      value: '',
      use: {
        phone: '', // 
        code: '' // 
      },
      rules: {
        phone: [
          // 
          { required: true, message: '', trigger: 'onBlur' },
          // 
          {
            pattern: /^(0|86|17951)?(13[0-9]|15[012356789]|166|17[3678]|18[0-9]|14[57])[0-9]{8}$/,
            message: '',
            trigger: 'onBlur'
          }
        ],
        code: [
          // 
          { required: true, message: '', trigger: 'onBlur' },
          //  4
          {
            pattern: /^\d{4}$/,
            message: ' 4',
            trigger: 'onBlur'
          }
        ]
      }
    }
  },
  methods: {
    login () {
      //  form 
      // validate(): 
      //  then .then  catch
      this.$refs.myglobform
        .validate()
        .then(() => {
          console.log('')
        })
        .catch(err => {
          console.log(err)
        })
    },
    event () {
      console.log('')
    },
    checkphone () {
      this.$refs.myform
        .validate(['phone'])
        .then(() => {
          console.log('')
        })
        .catch(err => {
          console.log(err)
        })
    },
    toast1 () {
      this.$toast('')
    },
    toast2 () {
      this.$toast.loading({
        message: ''
      })
    },
    toast3 () {
      this.$toast.success({
        message: ''
      })
    },
    toast4 () {
      this.$toast.fail({
        message: ''
      })
    },
    toast5 () {
      this.$toast({
        type: 'loading', // success & fail
        message: '',
        duration: 0
      })
    },
    clear () {
      this.$toast.clear()
    }
  }
}
</script>

<style lang="less" scoped>
.usevant {
  font-size: 12px;
}
.btn {
  padding: 0 15px;
  button {
    width: 100%;
    height: 44px;
    background-color: #e40137;
    border-radius: 30px;
    color: #fff;
    border: none;
  }
}
h2 {
  font-size: 18px;
}
h3 {
  font-size: 14px;
}
/deep/ .van-nav-bar__left {
  padding: 0;
  .myarrow05 {
    font-size: 40px;
  }
}
</style>
