<template>
  <div class="edifInfo">
    <!--  -->
    <hmnavbar
      :title="title"
      @back="back"
      @save="save"
      :hassave="user[prop] !== value"
    ></hmnavbar>
    <!--  -->
    <div class="ipt" v-if="prop !== 'avatar'">
      <van-field v-model="value" />
    </div>
    <!--  -->
    <div class="upload" v-else>
      <van-uploader
        v-model="fileList"
        :before-read="beforRead"
        :after-read="afterRead"
        :max-count="1"
      />
    </div>
  </div>
</template>

<script>
import { mapState, mapActions } from 'vuex'
// 
import { editUserApi, uploadApi } from '../../../api/login'
export default {
  data () {
    return {
      // 
      value: '',
      // 
      title: '',
      // 
      prop: '',
      // 
      fileList: []
    }
  },
  computed: {
    ...mapState(['user'])
  },
  methods: {
    ...mapActions(['setUser']),
    // 
    async afterRead (file) {
      // file
      // 
      // console.log(file.file)
      const res = await uploadApi(file.file)
      //  id
      const id = res.data[0].id
      this.value = id
    },
    // 
    beforRead (file) {
      // 
      //   file.type jpg
      const isType = file.type === 'image/jpeg'
      //   file.size 2m
      const isSize = file.size / 1024 / 1024 < 2
      if (!isType) {
        this.$toast.fail(' jpg ')
      }
      if (!isSize) {
        this.$toast.fail(' 2m ')
      }
      return isType && isSize
    },
    // 
    back () {
      // this.$router.push('/info')
      this.$router.back()
    },
    // 
    async save () {
      // 
      this.$toast({
        type: 'loading',
        message: '...',
        duration: 0
      })
      // 1.0 : value
      // 2.0 
      await editUserApi({
        [this.prop]: this.value
      })
      // 3.0 
      this.setUser()
      // 4.0 
      // this.$router.push('/info')
      this.$router.back()
      this.$toast.clear()
    }
  },
  // 
  created () {
    //  prop
    this.prop = this.$route.query.prop
    //  prop
    if (this.prop === 'nickname') {
      // 
      this.title = ''
    } else if (this.prop === 'intro') {
      this.title = ''
    } else if (this.prop === 'position') {
      this.title = ''
    } else if (this.prop === 'avatar') {
      this.title = ''
      //  fileList
      this.fileList.push({
        url: process.env.VUE_APP_URL + this.user.avatar
      })
    }
    // 
    this.value = this.user[this.prop]
  }
}
</script>

<style lang="less" scoped>
.edifInfo {
  .ipt {
    padding: 15px;
  }
  /deep/ .van-cell.van-field {
    background-color: #f7f4f5;
    border-radius: 4px;
  }
  .upload {
    text-align: center;
    /deep/ .van-image.van-uploader__preview-image,
    /deep/ .van-uploader__upload {
      width: 200px;
      height: 200px;
    }
  }
}
</style>
