<template>
  <div class="find">
    <!--  -->
    <van-nav-bar>
      <template #title>
        <h3></h3>
      </template>
    </van-nav-bar>
    <!--  -->
    <van-pull-refresh v-model="isLoading" @refresh="refresh">
      <!--  -->
      <div class="skill">
        <findcell title=""></findcell>
        <div class="items">
          <technicItem
            :item="item"
            v-for="(item, index) in technicList"
            :key="index"
          ></technicItem>
        </div>
      </div>
      <!--  -->
      <div class="data">
        <findcell title="" @more="toMarket"></findcell>
        <div class="tags">
          <div class="tag">{{ city }}</div>
          <div class="tag">{{ position }}</div>
        </div>
        <div class="items">
          <div class="item" v-for="(item, index) in showList" :key="index">
            <div class="left">{{ item.year }}</div>
            <div class="center">
              <div
                class="content"
                :style="{
                  width: Math.round((item.salary / topSalary) * 100) + '%'
                }"
              >
                {{ item.salary }}
              </div>
            </div>
            <div class="right">
              <div v-if="item.percent">
                <i
                  v-if="item.percent < 0"
                  style="color: red;"
                  class="iconfont iconicon_xiajiang"
                ></i>
                <i
                  v-else
                  style="color: green;"
                  class="iconfont iconicon_shangsheng"
                ></i>
                {{ Math.abs(item.percent) }}%
              </div>
            </div>
          </div>
        </div>
        <div class="extend">
          <span ref="mymore" v-if="isOpen === false" @click="openmore"
            ><i class="iconfont iconicon_zhankai"></i
          ></span>
          <span v-else @click="closemore"
            ><i class="iconfont myicon iconicon_zhankai"></i
          ></span>
        </div>
      </div>
      <!--  -->
      <div class="share">
        <findcell
          @more="$router.push('/shareList')"
          title=""
        ></findcell>
        <div class="items">
          <shareItem
            :item="item"
            v-for="(item, index) in shareList"
            :key="index"
          ></shareItem>
        </div>
      </div>
      <div class="foot">
        --  --
      </div>
    </van-pull-refresh>
  </div>
</template>

<script>
import findcell from './findcell'
import technicItem from './technicItem'
// 
import { getTechnicApi, getHotApi, getShareApi } from '../../../api/articles.js'
export default {
  name: 'find',
  data () {
    return {
      // 
      isOpen: false,
      // 
      technicList: [],
      // 
      hotList: [],
      city: '',
      position: '',
      topSalary: 0,
      // 
      showList: [],
      // 
      shareList: [],
      // 
      isLoading: false
    }
  },
  // 
  created () {
    this.getAllData()
  },
  methods: {
    // 
    toMarket () {
      this.$router.push('/market')
    },
    // 
    refresh () {
      // 
      this.isOpen = false
      this.technicList = []
      this.hotList = []
      this.city = ''
      this.position = ''
      this.topSalary = 0
      this.showList = []
      this.shareList = []
      // 
      this.getAllData()
    },
    async getAllData () {
      // 
      const resTechnic = await getTechnicApi({
        start: 0,
        limit: 3
      })
      // 
      this.technicList = resTechnic.data.list
      // 
      const resHot = await getHotApi()
      // 
      // 
      this.hotList = resHot.data.yearSalary.reverse()
      this.city = resHot.data.city
      this.position = resHot.data.position
      this.topSalary = resHot.data.topSalary
      this.showList = this.hotList.slice(0, 4)
      // 
      const resShare = await getShareApi({
        start: 0,
        limit: 3
      })
      // 
      this.shareList = resShare.data.list
      //  false
      this.isLoading = false
    },
    // 
    openmore () {
      this.isOpen = true
      //  7
      this.showList = this.hotList
    },
    // 
    closemore () {
      this.isOpen = false
      //  4
      this.showList = this.hotList.slice(0, 4)
    }
  },
  // 
  activated () {
    // 
    window.scrollTo(0, this.$route.meta.scrollTop)
  },
  components: {
    findcell,
    technicItem
  }
}
</script>

<style lang="less" scoped>
.find {
  background-color: #f7f4f5;
  padding-bottom: 50px;
  .skill {
    margin-top: 4px;
    background-color: #fff;
    .items {
    }
  }
  .data {
    background-color: #fff;
    margin-top: 10px;
    .tags {
      padding: 25px 15px 20px;
      display: flex;
      flex-wrap: wrap;
      .tag {
        font-size: 14px;
        background: #eceaea;
        padding: 4px 8px;
        margin-right: 10px;
      }
    }
    .items {
      .item {
        display: flex;
        align-items: center;
        padding: 10px 15px;
        .left {
          font-size: 14px;
        }
        .center {
          flex: 1;
          height: 12px;
          background-color: #ebdfdf;
          border-radius: 10px;
          margin: 0 10px;
          .content {
            background-color: #fe6d67;
            border-radius: 10px;
            font-size: 12px;
            line-height: 12px;
            color: #fff;
            text-align: right;
          }
        }
        .right {
          font-size: 14px;
          width: 55px;
        }
      }
    }
    .extend {
      display: flex;
      padding: 15px 0px;
      align-items: center;
      justify-content: center;
      font-size: 14px;
      i {
        font-size: 12px;
        margin-left: 6px;
      }
      .myicon {
        display: inline-block;
        transform: rotate(180deg);
      }
    }
  }
  .share {
    margin-top: 10px;
    background-color: #fff;
    .items {
    }
  }
  .foot {
    color: #ccc;
    font-size: 14px;
    text-align: center;
    line-height: 50px;
  }
}
</style>
